<template>
  <div id="app">
    <el-row>
      <el-col :span="7" class="pos-order" id="posorder">
        <el-tabs id="pane">
          <el-tab-pane label="点餐">
            <el-table :data="tableData" border>
              <el-table-column prop="goodsName" label="商品名称" width="170"></el-table-column>
              <el-table-column prop="count" label="数量" width="100"></el-table-column>
              <el-table-column prop="price" label="单价"></el-table-column>
              <el-table-column prop label="操作" width="150" fixed="right">
                <template slot-scope="scope">
                  <el-button type="danger" size="small" @click="deleteSingleGood(scope.row)">删除</el-button>
                  <el-button type="primary" size="small" @click="addOrderList(scope.row)">增加</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="totaldiv">
              <div class="totalLeft">
                <small>数量</small>
                ：{{totalcount}} &nbsp; &nbsp; &nbsp;
                <small>金额</small>
                ：{{totalmoney}}
              </div>
            </div>
            <div id="btn-list">
              <el-button type="info" size>挂单</el-button>
              <el-button type="warning" size @click="deleteAllgoods">删除</el-button>
              <el-button type="success" size @click="checkout">结账</el-button>
              <el-button type="primary" size @click="dialogVisible = res;" v-show="res">扫码支付</el-button>
              <!--付款吗-->
              <el-dialog
                :title="`请扫码 一共￥`+totalmoney"
                :visible.sync="dialogVisible"
                width="50%"
                :before-close="handleClose"
              >
                <span>
                  <img width="400px" height="600px" :src="imgUrl" />
                </span>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="dialogVisible = false">取消扫码</el-button>
                  <el-button type="primary" @click="successs">付款成功</el-button>
                </span>
              </el-dialog>
            </div>
          </el-tab-pane>
          <el-tab-pane label="挂单">挂单</el-tab-pane>
          <el-tab-pane label="外卖">外卖</el-tab-pane>
        </el-tabs>
      </el-col>
      <el-col :span="17">
        <div class="often-goods">
          <div class="title">常用商品</div>
          <div class="often-goods-list">
            <ul>
              <li v-for="goods in oftenGoods2" @click="addOrderList(goods)">
                <span>{{ goods.goodsName }}</span>
                <span class="oprice">￥{{ goods.price }}元</span>
              </li>
            </ul>
          </div>
        </div>

        <!--图片商品选购栏 -->
        <div class="img">
          <el-row>
            <el-col>
              <el-tabs class="imggoods-nav">
                <el-tab-pane class="imggoods-pane" label="汉堡">
                  <ul>
                    <li v-for="goods in type0Goods2" @click="addOrderList(goods)">
                      <span class="imggoods-img">
                        <img width="150px" height="150px" :src="goods.goodsImg" alt />
                      </span>
                      <span class="imggoods-name">{{goods.goodsName}}</span>
                      <span class="imggoods-price">￥{{goods.price}}元</span>
                    </li>
                  </ul>
                </el-tab-pane>
                <el-tab-pane class="imggoods-pane" label="小食">
                  <ul>
                    <li v-for="goods in type0Goods2" @click="addOrderList(goods)">
                      <span class="imggoods-img">
                        <img width="150px" height="150px" :src="goods.goodsImg" alt />
                      </span>
                      <span class="imggoods-name">{{goods.goodsName}}</span>
                      <span class="imggoods-price">￥{{goods.price}}元</span>
                    </li>
                  </ul>
                </el-tab-pane>
                <el-tab-pane label="饮料"></el-tab-pane>
                <el-tab-pane label="套餐"></el-tab-pane>
              </el-tabs>
            </el-col>
          </el-row>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import zfb from "../../assets/zhifubao.png";
export default {
  name: "pos",
  data() {
    return {
      totalmoney: 0,
      totalcount: 0,
      imgUrl: zfb,
      res: false,
      dialogVisible: false,
      tableData: [],
      tableData2: [
        {
          goodsId: 1,
          goodsName: "可口可乐",
          price: 8,
          count: 1
        },
        {
          goodsId: 2,
          goodsName: "香辣鸡腿堡",
          price: 15,
          count: 1
        },
        {
          goodsId: 3,
          goodsName: "爱心薯条",
          price: 8,
          count: 1
        },
        {
          goodsId: 4,
          goodsName: "甜筒",
          price: 8,
          count: 1
        }
      ],
      oftenGoods: [],
      oftenGoods2: [
        {
          goodsId: 1,
          goodsName: "香辣鸡腿堡",
          price: 18
        },
        {
          goodsId: 2,
          goodsName: "田园鸡腿堡",
          price: 15
        },
        {
          goodsId: 3,
          goodsName: "和风汉堡",
          price: 15
        },
        {
          goodsId: 4,
          goodsName: "快乐全家桶",
          price: 80
        },
        {
          goodsId: 5,
          goodsName: "脆皮炸鸡腿",
          price: 10
        },
        {
          goodsId: 6,
          goodsName: "魔法鸡块",
          price: 20
        },
        {
          goodsId: 7,
          goodsName: "可乐大杯",
          price: 10
        },
        {
          goodsId: 8,
          goodsName: "雪顶咖啡",
          price: 18
        },
        {
          goodsId: 9,
          goodsName: "大块鸡米花",
          price: 15
        },
        {
          goodsId: 10,
          goodsName: "香脆鸡柳",
          price: 17
        }
      ],
      type0Goods: [],
      type0Goods2: [
        {
          goodsId: 2,
          goodsImg:
            "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=2482815880,2072226004&fm=26&gp=0.jpg",
          goodsName: "田园鸡腿堡",
          price: 15
        },
        {
          goodsId: 1,
          goodsImg:
            "https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1829009049,2193113771&fm=26&gp=0.jpg",
          goodsName: "香辣鸡腿堡",
          price: 18
        },
        {
          goodsId: 3,
          goodsImg:
            "https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1714535156,3897596086&fm=26&gp=0.jpg",
          goodsName: "和风汉堡",
          price: 15
        },
        {
          goodsId: 4,
          goodsImg:
            "https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=2082733465,911826084&fm=26&gp=0.jpg",
          goodsName: "快乐全家桶",
          price: 80
        },
        {
          goodsId: 5,
          goodsImg:
            "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=504923401,3560811669&fm=26&gp=0.jpg",
          goodsName: "脆皮炸鸡腿",
          price: 10
        },
        {
          goodsId: 7,
          goodsImg:
            "https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=4192781040,2035329144&fm=26&gp=0.jpg",
          goodsName: "可乐大杯",
          price: 10
        },
        {
          goodsId: 6,
          goodsImg:
            "https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=2001834384,1242456503&fm=26&gp=0.jpg",
          goodsName: "魔法鸡块",
          price: 20
        },

        {
          goodsId: 9,
          goodsImg:
            "https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=2581842065,1312867574&fm=26&gp=0.jpg",
          goodsName: "大块鸡米花",
          price: 15
        },
        {
          goodsId: 8,
          goodsImg:
            "https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1322895546,706612189&fm=11&gp=0.jpg",
          goodsName: "雪顶咖啡",
          price: 18
        },

        {
          goodsId: 20,
          goodsImg:
            "https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=2581842065,1312867574&fm=26&gp=0.jpg",
          goodsName: "香脆鸡柳",
          price: 17
        }
      ]
    };
  },
  mounted: function() {
    var orderHeight = document.body.clientHeight;
    document.getElementById("posorder").style.height = orderHeight + "px"; //记得加单位
  },
  methods: {
    addOrderList(goods) {
      //将商品count money清零
      this.totalcount = 0;
      this.totalmoney = 0;
      //商品是否已经存在于订单列表中
      let isHave = false;
      for (let i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].goodsId == goods.goodsId) {
          isHave = true;
        }
      }
      //根据判断的值写业务逻辑
      if (isHave) {
        //改变列表中商品的数量 加1
        let arr = this.tableData.filter(o => o.goodsId == goods.goodsId);
        arr[0].count++;
      } else {
        let newGoods = {
          goodsId: goods.goodsId,
          goodsName: goods.goodsName,
          price: goods.price,
          count: 1
        };
        this.tableData.push(newGoods);
      }
      //计算汇总金额数量
      this.getAllMoney();
      //扫码支付显示
      if (this.totalcount != 0) {
        this.res = true;
      }
    },

    deleteSingleGood(goods) {
      //this.tableData = this.tableData.filter(x => x.goodsId != goods.goodsId);
      //改变列表中商品的数量 减一
      let arr = this.tableData.filter(o => o.goodsId == goods.goodsId);
      arr[0].count--;
      console.log(arr[0]);
      if (arr[0].count == 0) {
        this.tableData = this.tableData.filter(o => o.goodsId != goods.goodsId);
      }
      this.getAllMoney();
    },
    deleteAllgoods() {
      this.tableData = [];
      this.totalcount = 0;
      this.totalmoney = 0;
    },

    getAllMoney() {
      this.totalcount = 0;
      this.totalmoney = 0;
      if (this.tableData) {
        //计算汇总金额数量
        this.tableData.forEach(x => {
          this.totalcount += x.count;
          this.totalmoney = this.totalmoney + x.price * x.count;
        });
      }
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    checkout() {
      //模拟结账
      if (this.totalcount != 0) {
        this.tableData = [];
        this.totalcount = 0;
        this.totalmoney = 0;
        this.$message({
          message: `结账成功`,
          type: "success"
        });
      } else {
        this.$message({
          message: "您还没有点餐哦~",
          type: "error"
        });
      }
    },
    successs() {
      if (this.totalcount != 0) {
        this.tableData = [];
        this.totalcount = 0;
        this.totalmoney = 0;
        this.$message({
          message: `欢迎下次光临`,
          type: "success"
        }).then((this.dialogVisible = false));
      }
    }
  }
};
</script>
<style scoped>
.pos-order {
  background: #f9fafc;
  border-right: 1px solid #c0ccda;
}
#pane {
  margin-left: 10px;
}
#btn-list {
  margin-top: 10px;
}
.often-goods .title {
  height: 30px;
  line-height: 30px;
  text-align: left;
  padding: 10px;
  border-bottom: 1px solid #c0ccda;
  background: #f9fafc;
}
.totaldiv {
  background: #fff;
  border-bottom: 1px solid #c0ccda;
  padding: 15px;
  font-size: 18px;
}

.often-goods .often-goods-list ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.often-goods .often-goods-list ul li {
  float: left;
  border: 1px solid #e5e9f2;
  margin: 10px;
  padding: 5px;
  width: 23%;
  overflow: hidden;
  background: #fff;
  cursor: pointer;
}
.often-goods .often-goods-list ul li .oprice {
  color: #58b7ff;
}
.img {
  clear: both;
  margin-left: 15px;
}
.imggoods-nav {
  font-size: 16px;
}
.imggoods-pane {
  margin-left: -30px;
}
.imggoods-nav ul li {
  float: left;
  list-style: none;
  border: 1px solid #c0ccda;
  width: 23%;
  margin: 10px;
  background-color: #fff;
  cursor: pointer;
}
.imggoods-nav ul li .imggoods-name {
  padding-left: 10px;
  color: brown;
  font-size: 26px;
  padding-top: 10px;
}
.imggoods-nav ul li .imggoods-price {
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
}
.imggoods-nav ul li span {
  display: block;
  float: left;
}
.imggoods-nav ul li .imggoods-img {
  width: 50%;
}
</style>


